body {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(#51d381, rgb(97, 255, 252), rgb(0, 57, 200));
    font-family: Arial, sans-serif;
  }
  
  .container {
    height: 650px;
    width: 500px; /* Zwiększenie szerokości kontenera */
    max-width: 100%;
    text-align: center;
    border: 2px solid #ddd;
    border-radius: 20px;
    padding: 20px;
    background-image: url('./bg_photo.jpg');
    background-size: cover;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .profile-pic {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 3px solid #ddd;
    object-fit: cover;
  }
  
  .profile-name {
    margin-top: 10px;
    font-size: 25px;
    font-weight: bold;
    color: #000;
  }
  
  h1 {
    margin-bottom: 20px;
    font-size: 24px;
    color: #fff;
  }
  
  .icon-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .icon-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .icon {
    align-items: center;
    justify-content: center;
    width: 25vh;
    font-size: 50px;
    text-decoration: none;
    padding: 10px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .icon span {
    margin-left: 10px;
    font-size: 20px;
    color: #fff;
  }
  
  .icon.instagram {
    background-color: rgba(228, 64, 95, 0.5);
    color: #fff;
  }
  
  .icon.instagram:hover {
    background-color: rgba(228, 64, 95, 1);
    color: #fff;
  }
  
  .icon.snapchat {
    background-color: rgba(255, 252, 0, 0.5);
    color: #000;
  }
  
  .icon.snapchat:hover {
    background-color: rgba(255, 252, 0, 1);
    color: #000;
  }
  
  .icon.phone {
    background-color: rgba(37, 211, 102, 0.5);
    color: #fff;
  }
  
  .icon.phone:hover {
    background-color: rgba(37, 211, 102, 1);
    color: #fff;
  }
  
  /* Responsywność */
  @media (max-width: 600px) {
    .container {
      width: 100%;
      padding: 10px;
    }
  
    .icon {
      font-size: 30px;
    }
  
    .icon span {
      font-size: 16px;
    }
  
    h1 {
      font-size: 20px;
    }
  
    .profile-pic {
      width: 100px;
      height: 100px;
    }
  
    .profile-name {
      font-size: 18px;
    }
  }
  